<template>
  <div class="page-view">
    <navbar :title="'个人资料'" slot="navbar"></navbar>
    <div class="user-header">
    <yd-flexbox  direction="vertical">
      <yd-flexbox-item>
        <img :src="user.user_img" alt="">
        <p>
          {{user.nickname}}
        </p>
      </yd-flexbox-item>
    </yd-flexbox>
    </div>
    <yd-cell-group>
      <yd-cell-item arrow type="a" >
        <span slot="left">姓名：</span>
        <span slot="right">{{user.realname}}</span>
      </yd-cell-item>
      <yd-cell-item arrow type="a" >
        <span slot="left">邮箱：</span>
        <span slot="right">{{user.email}}</span>
      </yd-cell-item>
      <yd-cell-item arrow type="a" >
        <span slot="left">职业：</span>
        <span slot="right">HLP算法工程师</span>
      </yd-cell-item>
    </yd-cell-group>
  </div>
</template>

<script>
    import Navbar from '../../components/Navbar.vue'
    export default{
      components:{
        Navbar
      },
      computed:{
        user(){
          return this.$store.state.user
        }
      }
    }
</script>

<style scoped lang="less" rel="stylesheet/less">
  .cell-item{
    padding: 0 30px;
  }
  .cell-item:not(:last-child):after{
    margin-left: 0;
  }
</style>



